{
  const hamburgerButton = document.querySelector('.js-hamburgerButton');
  const sideNavigationButton = document.querySelector('.js-sideNavigationButton');
  const navigationList = document.querySelector('.js-navigationList');
  const navigationLinks = document.querySelectorAll('.js-navigationLink');

  const fadeLinksMobile = () => {
    navigationLinks.forEach( link => {
      if ( window.matchMedia('(max-width: 767px)').matches && !link.classList.contains('linksFade') ) {
        link.classList.add('linksFade');
      } else {
        link.classList.remove('linksFade');
      }
    })
  }

  const toggleClasses = () => {
    hamburgerButton.classList.toggle('js-active');
    navigationList.classList.toggle('navigation__list--open');
  }

  const setHamburgerButton = () => {
    toggleClasses();
    fadeLinksMobile();

    if (navigationList.classList.contains('navigation__list--open')) {
      hamburgerButton.setAttribute('aria-expanded', true); 
    } else {
      hamburgerButton.setAttribute('aria-expanded', false);
      sideNavigationButton.setAttribute('aria-expanded', false);
    }
  }

  const setTextSideNavigationButton = () => {
    const arrowRight = 'ткв';
    const arrowLeft = 'ткб';
    sideNavigationButton.innerText = sideNavigationButton.innerText === arrowRight ? arrowLeft : arrowRight;
  }

  const getNavigationButtonActive = () => {
    toggleClasses();
    
    if (navigationList.classList.contains('navigation__list--open')) {
      sideNavigationButton.setAttribute('aria-expanded', true)
    } else {
      sideNavigationButton.setAttribute('aria-expanded', false);
      hamburgerButton.setAttribute('aria-expanded', false);
    }
    fadeLinksMobile();
    setTextSideNavigationButton();
  }

  const resetStatesWhenLinkPressed = () => {
    navigationLinks.forEach( link => {
      link.addEventListener('click', () => {
        navigationList.classList.remove('navigation__list--open');
        hamburgerButton.classList.remove('js-active');
        sideNavigationButton.setAttribute('aria-expanded', false);
        hamburgerButton.setAttribute('aria-expanded', false);
        fadeLinksMobile();
      })
    })
  }

  removeFadeLinks = () => {
    window.pageYOffset > 200 ? sideNavigationButton.classList.add('navigation__list-sideNavigationButton--active') : sideNavigationButton.classList.remove('navigation__list-sideNavigationButton--active');
  }


  // theme mode & switcher

  const modeSwitcher = document.querySelector('.js-switcherMode');
  const body = document.querySelector('body').classList; 

  const changeTextSwitcher = () => {
    const textSwitcher = document.querySelector('.switcher__text');
    textSwitcher.innerText = textSwitcher.innerText === 'light' ? 'dark' : 'light';
  }

  const enableDarkMode = () => {
    body.add('dark');
    changeTextSwitcher();
    localStorage.setItem( 'theme', 'dark' );
    modeSwitcher.checked = true;
    modeSwitcher.setAttribute( 'aria-checked', true );
  }

  const disableDarkMode = () => {
    // toggleTheme();
    body.remove('dark');
    changeTextSwitcher();
    localStorage.setItem( 'theme', 'light' );
    modeSwitcher.checked = false;
    modeSwitcher.setAttribute( 'aria-checked', false );
  }

  const toggleThemeButton = theme => {
    theme = localStorage.getItem( 'theme' );
    theme === 'light' ? enableDarkMode() : disableDarkMode();
  }

  const getUserThemePreferences = () => {
    const userThemePreference = window.matchMedia( '(prefers-color-scheme: dark)' ).matches;
    userThemePreference ? enableDarkMode() : disableDarkMode();
  }

  const checkTheme = theme => {
    if ( theme ) {
      theme === 'dark' ? enableDarkMode() : disableDarkMode();
    } else {
      getUserThemePreferences();
    }
  }

  const onFormSubmit = event => {
    event.preventDefault;
  }

  const init = () => {
    const submitButton = document.querySelector('.js-submitButton');
    const theme = localStorage.getItem( 'theme' );
  
    hamburgerButton.addEventListener('click', setHamburgerButton);
    sideNavigationButton.addEventListener('click', getNavigationButtonActive);
    resetStatesWhenLinkPressed();
    window.addEventListener( 'scroll', removeFadeLinks);
    checkTheme(theme);
    modeSwitcher.addEventListener('click', toggleThemeButton);
    submitButton.addEventListener('submit', onFormSubmit);
  }

  init();
}